    display: flex;
} */

.Recipe_recipeCard__3QUZ1 li {
    padding: 5px 10px;
    background: linear-gradient(182deg, #c781f7b3, #af52ee);
    border-radius: 50px;
    margin-right: 10px;
    font-style: italic;
    box-shadow: 0px 0px 10px 0px #ad78d199, inset 1px -2px 4px 0px #0d0c0e99;
}
html {
    height: 100vh;
    width: 100%;
    text-align: center;
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
    background-attachment: fixed;
    font-family: 'Poppins', sans-serif;
}

.App {
    text-align: center;
}


/* width */

::-webkit-scrollbar {
    width: 15px;
    position: absolute;
}


/* Track */

::-webkit-scrollbar-track {
    background: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.3);
    border-radius: 5px;
}


/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.4);
    border: none;
}

.search-form {
    margin-top: 20px;
    width: 100%;
    min-height: 10vh;
}

.seach-area {
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.search-bar {
    width: 50%;
    border: none;
    padding: 10px;
    border-radius: 20px 0px 0px 20px;
    box-shadow: 0px 0px 10px rgba(128, 128, 128, 0.5);
    outline: none;
    transition: all 500ms;
    font-family: 'Poppins', sans-serif;
}

.search-bar:focus {
    border: 1px solid rgba(202, 28, 28, 0);
    outline: none;
}

.search-button {
    color: white;
    background: #a18cd1;
    border: none;
    padding: 10px;
    border-radius: 0px 20px 20px 0px;
    box-shadow: 0px 0px 10px rgba(128, 128, 128, 0.5);
    font-family: 'Poppins', sans-serif;
}

select {
    color: white;
    margin: 10px;
    /* width: 10vw; */
    padding: 10px;
    outline: none;
    border: none;
    border-radius: 20px;
    font-family: 'Poppins', sans-serif;
    box-shadow: 0px 0px 10px rgba(128, 128, 128, 0.5);
    background-color: #a18cd1;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

option {
    width: -webkit-max-content;
    width: max-content;
    font-size: 15px;
    padding: 10px;
    outline: none;
    border: 10px solid red;
    background: #a18cd100;
}

.recipe-cards {
    margin-top: 30px;
    display: grid;
    grid-template-columns: 1fr;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
    justify-items: center;
    margin: 0px 20px;
}

#no-result {
    width: 100px;
    height: 100px;
}
